.file-upload {
  background-color: rgba(255, 255, 255, 0.236);
  padding: 30px;
  border-radius: 10px;
  width: 370px;
  text-align: center;
  color: white;
  margin: auto;

  -webkit-border-radius: 20px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.file-upload h3 {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.file-upload input[type="file"] {
  margin-bottom: 10px;
}

.error-message {
  color: rgb(255, 0, 0);
  font-size: 0.9em;
  margin-top: 10px;
}

.success-message {
  color: rgb(0, 255, 0);
  font-size: 0.9em;
  margin-top: 10px;
}

.upload-button {
  background-color: rgb(128, 0, 255);
  color: white;
  padding: 11px 18px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.upload-button:hover {
  background-color: rgb(24, 0, 39);
}
